<template>
    <div class="margin-tb-10 margin-lr-20 layout-content">
        <div class="margin-b-10">
            <apply-steps :active-step="orderStep" :first-text="title"></apply-steps>
        </div>
        <div class="cm-panel" style="min-height: 620px" v-if="inited">
            <i-form :model="formData" ref="formData" :label-width="60" :rules="ruleValidate">
                <Row>
                    <div>
                        <Row class="font0">
                            <i-col :xs="16" :sm="16" :md="12" :lg="10">
                                <Form-item label="选择渠道" prop="channelCode">
                                    <i-select type="text" v-model="formData.channelCode" :disabled='isChange=="1"'
                                              placeholder="请选择"
                                              @on-change="changeChannel">
                                        <i-option v-for="(item,index) in DICT_channelList" :value="item.channelCode"
                                                  :key="item.channelCode">
                                            {{item.channelName}}
                                        </i-option>
                                    </i-select>
                                </Form-item>
                            </i-col>
                            <i-col :xs="8" :sm="8" :md="6" :lg="5">
                                <Form-item class="form-item-nolabel margin-l-5" prop="reportSubject">
                                    <i-select type="text" v-model="formData.reportSubject"
                                              :disabled="formData.channelCode=='' || isChange=='1'" placeholder="请选择"
                                              @on-change="changeGszt">
                                        <i-option v-for="(item,index) in DICT_channelChildList"
                                                  :value="item.reportSubject" :key="item.reportSubject">
                                            {{item.reportSubjectName}}
                                        </i-option>
                                    </i-select>
                                </Form-item>
                            </i-col>
                        </Row>
                    </div>
                    <div>
                        <Row>
                            <i-col :xs="24" :sm="16" :md="12" :lg="10">
                                <Form-item label="选择产品" class="" prop="productCode">
                                    <i-input style="display:none" v-model="formData.productCode"></i-input>
                                    <i-input v-model="formData.productName" :disabled="productInputDisabled"
                                             @click.native="openProdModalFn"
                                             @on-change="changeProduct" readonly placeholder="请选择">
                                        <Button slot="append" icon="ios-arrow-forward" class="self-append-btn"></Button>
                                    </i-input>
                                </Form-item>
                            </i-col>
                        </Row>
                    </div>
                    <div>
                        <Row>
                            <i-col :xs="24" :sm="16" :md="12" :lg="10">
                                <FormItem label="选择车型" v-clickoutside:click="outsideCarInputFn" prop="carStyleId">
                                    <div class="carpicker-input-container">
                                        <i-input v-model="formData.carTypeId" style="display:none"></i-input>
                                        <div class="search-input-con">
                                            <Button type="text" custom-icon="iconfont icon-search"
                                                    class="search-input-icon"
                                                    @click.native="carstyleInputChange()"></Button>
                                            <i-input v-model="carFullText" :disabled="carInputDisabled"
                                                     readonly @click.native="carstyleInputChange()"
                                                     placeholder="请选择">
                                            </i-input>
                                        </div>
                                        <div class="carpicker-panel-container" v-show="showCarPanel">
                                            <car-style-picker :visible="formData.productCode!=''"
                                                              :carData="formData"
                                                              :productCode="formData.productCode"
                                                              :onlyLastModel="formData.carModifyStatus == '2'"
                                                              @on-change="carpickerChange"></car-style-picker>
                                        </div>
                                    </div>
                                </FormItem>
                            </i-col>
                        </Row>

                        <Row class="text-center font0">
                            <i-button size="large" @click="goBack">返回</i-button>
                            <i-button type="primary" size="large" @click="saveFullPreChooseFn()"
                                      v-if="!isChange&&!isQy">提交预审
                            </i-button>
                            <i-button type="primary" size="large" @click="saveFullPreChooseFn()"
                                      v-if="isChange||isQy">下一步
                            </i-button>
                        </Row>
                    </div>
                </Row>
            </i-form>
        </div>

        <!--提交预审返回信息弹窗-->
        <Modal title="提示信息" v-model="showResModal" :mask-closable="false" :closable="false">
            <p class="font18" style="margin-top:-10px;">订单<span class="color-primary">{{orderId}}</span>提交预审成功！</p>
            <p>返回订单列表后可查看预审进度~</p>
            <div slot="footer">
                <i-button type="primary" @click="backToList">确 定</i-button>
            </div>
        </Modal>
        <!--产品选择弹窗-->
        <prod-single-select-modal v-if="prodModalCreate" :channelCode="formData.channelCode"
                                  :reportSubject="formData.reportSubject" v-model="showProdModal"
                                  @on-close="showProdModal=false"
                                  @on-select="selectProdResFn"></prod-single-select-modal>

        <div class="simple-breadcrumb col-xs-11">
            <router-link to="/order/list">进件管理</router-link>
            <Icon type="ios-arrow-forward"></Icon>
            <span>{{title}}</span>
        </div>
    </div>
</template>

<script>
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import applySteps from "@/components/apply/applySteps.vue";
    import carStylePicker from "@/components/apply/carStylePicker";
    import prodSingleSelectModal from "@/components/apply/prodSingleSelectModal";

    import {yx} from "@/utils/helper";
    import {Clickoutside} from '@/utils/clickoutside';
    import {validateRules} from '@/utils/validateRules';

    export default {
        name: "orderPreaudit",
        directives: {
            Clickoutside
        },
        components: {
            applySteps,
            carStylePicker,
            prodSingleSelectModal
        },
        data() {
            return {
                orderStep: 1, //当前节点
                account: this.$store.state.app.userInfo.userAccount,
                title: '预审', //预审及选择产品/车型的切换
                ruleValidate: validateRules.preauditPage, //表格校验规则
                showResModal: false, //显示返回结果弹窗
                originalData: {}, //保存修改预审时的原始信息
                formData: {
                    "orderId": "", //主订单号
                    "channelCode": "", //渠道编码
                    "channelName": "", //渠道简称名称
                    "mainName": "", //渠道全称名称
                    "reportSubject": "", //主体编码
                    "reportSubjectName": "", //主体名称
                    "productCode": "", //产品id
                    "productName": "", //产品名称
                    "productVersion": "", //产品版本号
                    "productLoanPeriodDesc": "", //产品融资期限  code值，不是描述
                    "productRateDesc": "", //产品利率   值，不是描述
                    "sysId": "2", //系统标识。app：1 ； pc：2
                    //以下字段不知道从哪里取，PC非必传
                    "traceId": "", //请求ID
                    //车型其他字段要
                    "carTypeId": "", //车型编号
                    "carMasterBrandId": "",
                    "carMasterBrandName": "",
                    "carBrandId": "",
                    "carBrandName": "",
                    "carModelId": "",
                    "carModelName": "",
                    "carStyleId": "",
                    "carStyleName": "",
                    "carSeat": [], //座位数
                    //企业的客户类型在新建时要默认保存
                    "customerType": "", //客户类型

                    //新增可修改状态
                    "productModifyStatus": "",
                    "carModifyStatus": "",


                    //不要的字段，暂时用来联调
                    account: this.$store.state.app.userInfo.userAccount,
                    equipmentrCode: "windows8",
                    network: "wifi",
                    operator: "移动",

                },
                //预审页查询信息的字段转为页面使用字段名
                fieldToForm: {
                    //"orderId": "orderNo",//主订单号
                    "channelCode": "dealerCode", //渠道编码
                    "channelName": "dealerSimpleName", //渠道简称名称
                    "mainName": "dealerName", //渠道全称名称
                    "reportSubject": "mainSubject", //主体编码
                    "reportSubjectName": "mainSubjectDesc", //主体名称
                    "productCode": "productCode", //产品id
                    "productName": "productName", //产品名称
                    "productVersion": "productVersion", //产品版本号
                    "productLoanPeriodDesc": "productLoanPeriod", //产品融资期限  code值，不是描述
                    "productRateDesc": "productRate", //产品利率   值，不是描述
                    //车型其他字段不要吗
                    "carTypeId": "carStyleId", //车型编号
                    //车型数据
                    "carMasterBrandId": "carMasterBrandId",
                    "carMasterBrandName": "carMasterBrandName",
                    "carBrandId": "carBrandId",
                    "carBrandName": "carBrandName",
                    "carModelId": "carModelId",
                    "carModelName": "carModelName",
                    "carStyleId": "carStyleId",
                    "carStyleName": "carStyleName",
                    "carSeat":"carSeat", // 车辆座位数
                    //新增可修改状态
                    "productModifyStatus": "productModifyStatus",
                    "carModifyStatus": "carModifyStatus",
                },
                showCarPanel: false, //是否显示车型下拉
                //产品选择相关的
                showProdModal: false, //是否显示产品选择弹窗
                prodModalCreate: false, //创建时才显示产品选择
                DICT_channelList: [], //渠道及主体的数据字典
                inited: false,
                orderId: '',
                isChange: '',
                isPersonal: '',
                isQy: '',

            }
        },
        computed: {
            //从渠道中获取对应主体
            DICT_channelChildList() {
                let channel = this.DICT_channelList.find(item => {
                    return item.channelCode == this.formData.channelCode
                })
                if (channel && channel.reportSubjectList)
                    return channel.reportSubjectList;
                return [];
            },
            //车型返显值
            carFullText() {
                if (this.formData.carMasterBrandId && this.formData.carBrandId && this.formData.carModelId && this.formData.carStyleId) {
                    return [this.formData.carMasterBrandName, this.formData.carBrandName, this.formData.carModelName && this.formData.carStyleName]
                        .join(' ');
                }
                return '';
            },
            //产品选择禁用
            productInputDisabled() {
                return this.formData.channelCode == '' || this.formData.reportSubject == ''
                    || (this.isChange && this.formData.productModifyStatus != '1');
            },
            //车型选择禁用
            carInputDisabled() {
                return this.formData.channelCode == '' || this.formData.reportSubject == '' || this.formData.productCode == ''
                    || (this.isChange && !(this.formData.carModifyStatus == '1' || this.formData.carModifyStatus == '2'));
            }
        },
        mounted() {
            //修改
            this.isChange = this.$route.query.change == '1';
            this.orderId = this.$route.query.orderNo;
            this.isPersonal = yx.isPersonal(this.$route.params.customerType);
            this.isQy = yx.isQy(this.$route.params.customerType);
            this.formData.traceId = this.orderId;//暂时要
            //判断是新建、预审、修改等状态
            let errorTip = (!this.isPersonal && !this.isQy) ? '未选择客户类型' : (this.isChange && !this.orderId) ? '未获取到订单号，请重新选择并进入' :
                '';
            if (errorTip) {
                return this.$Modal.error({
                    title: '提示',
                    content: errorTip,
                    onOk: () => {
                        this.$router.back();
                    }
                })
            }
            //修改顶部title
            this.title = this.isPersonal ? '预审' : this.isQy ? '创建订单' : '';
            if (this.isChange && this.isPersonal) this.title = '修改预审信息';
            else if (this.isChange && this.isQy) this.title = '修改产品车型';
            document.title = '进件提报-' + this.title;
            //赋值给表单
            this.formData.orderId = this.orderId || '';
            this.formData.customerType = this.$route.params.customerType;

            //初始化查询
            this.queryInit();

        },
        methods: {
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
            ]),
            //跳转页面
            go(routename, params) {
                this.$router.push({
                    name: routename,
                    params: params
                })
            },
            //清除表单所有数据
            fnReset(name) {
                this.$refs[name].resetFields();
            },
            //封装报错弹窗
            errorModal(content, onOk, okText) {
                let options = {
                    title: '提示',
                    content: content,
                    closable: false,
                    okText: okText || '确 定',
                    maskClosable: false,
                };
                if (onOk) options.onOk = onOk;
                this.$Modal.error(options);
            },
            //初始查询
            async queryInit() {
                //如果有订单号就请求，inited是请求到数据到再渲染页面
                if (this.orderId && this.isChange) {
                    let row = await this.queryOrderPreauditInfo();
                    if (!row)
                        return this.errorModal('没有该订单状态信息', () => {
                            this.$router.back();
                        })
                    else {
                        this.setOrderInfoToForm(row);
                    }
                } else this.inited = true;
                //初始请求渠道及主体
                this.queryChannelList();
            },
            //返回进件列表
            backToList() {
                this.$router.replace({
                    name: 'orderList',
                })
            },
            //返回上一页
            goBack() {
                this.$router.back()
            },
            //请求渠道及下属主体
            queryChannelList() {
                this.showLoading();
                this.$http.post(polarisserver + '/channel/list', {"account": this.account}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '渠道信息请求失败');
                    if (!result.data || !result.data.length) return this.errorModal('查询渠道信息无数据');
                    this.DICT_channelList = result.data || [];
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('渠道信息请求出错：', err);
                })
            },

            //初始有订单信息时将查询到的订单信息赋值给表单
            setOrderInfoToForm(row) {
                if (!row) {
                    return this.errorModal('没有该订单状态信息', () => {
                        this.$router.back();
                    })
                }
                if (row.productModifyStatus != '1' && !(row.carModifyStatus == '1' || row.carModifyStatus == '2')) {
                    return this.errorModal('该订单不允许修改产品和车型', () => {
                        this.$router.back();
                    })
                }
                let obj = this.fieldToForm;
                for (let p in obj) {
                    this.formData[p] = row[obj[p]] || "";
                    this.originalData[p] = row[obj[p]] || "";
                }
                this.inited = true;
            },
            //有订单号时先进行查询
            queryOrderPreauditInfo() {
                this.showLoading();
                return new Promise((resolve, reject) => {
                    this.$http.get(polarisserver + '/credit/getOrderModifyDetail/' + this.account + '/' + this.$route.query.orderNo).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '订单状态 请求返回格式不对');
                        const steps = ['level11', 'level12', 'level13', 'level14'];
                        let stepIndex = steps.indexOf(result.data.orderSubStage);
                        this.orderStep = stepIndex > -1 ? stepIndex + 1 : 1;//设置顶部节点
                        resolve(result.data);
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('请求订单状态出错：', err, () => {
                            this.$router.back();
                        })
                    })
                })
            },
            //提交方法封装
            submitFn(url, params, method) {
                this.showLoading();
                method = method || 'post';
                if (!!params) params.saveFrom = 'PC';
                let txt = this.isChange ? '修改产品/车型' : this.isQy ? '创建企业订单' : '发起预审';
                return new Promise((resolve, reject) => {
                    this.$http[method](url, params).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || txt + '请求失败');
                        resolve(result.data)
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal(txt + '请求出错：', err);
                        reject(err)
                    })
                })
            },
            //'2'是个人预审或重发预审，'1'是企业新建选择或订单修改选择
            //确认所有选择并提交，个人 预审是返回列表，企业新建是提交生成订单号，订单产品修改是带上数据 返回上一页订单
            saveFullPreChooseFn() {
                this.loading = true;
                let _this = this;
                this.$refs['formData'].validate((valid) => {
                        if (!valid) return this.$Message.error('表单填写未通过，请检查!');
                        let params = Object.assign({}, this.formData);
                        //个人和企业修改产品/车型后，返回编辑页并刷新
                        if (_this.isChange) {
                            let editParams = {
                                orderId: this.$route.query.orderNo,
                                productCode: this.formData.productCode,//产品编码
                                productVersion: this.formData.productVersion, //产品版本号
                                styleId: this.formData.carStyleId,//车型id
                                sysId: this.formData.sysId,//订单来源 app：1 ； pc：2
                                customerType: this.formData.customerType,//客户类型 企业：2，个人：1
                            };
                            this.submitFn(polarisserver + '/credit/updatePretrial', editParams, 'post').then((res) => {
                                this.$Modal.success({
                                    title: '提示',
                                    closable: false,
                                    maskClosable: false,
                                    content: '修改产品/车型成功',
                                    okText: '确认',
                                    onOk: () => {
                                        this.$router.back();
                                    }
                                });
                            })
                        }
                        //个人预审非修改返回列表
                        if (!_this.isQy && !_this.isChange) {
                            let Params = {
                                orderId: this.orderId,
                                traceId: this.formData.traceId,//跟踪编号
                                account: this.formData.account,
                                channelCode: this.formData.channelCode,//渠道编码
                                channelName: this.formData.channelName,//渠道简称名称
                                mainName: this.formData.mainName,//渠道全称名称
                                reportSubject: this.formData.reportSubject,//主体编码
                                reportSubjectName: this.formData.reportSubjectName,//主体名称
                                productCode: this.formData.productCode,//产品编码
                                productVersion: this.formData.productVersion, //产品版本号
                                carTypeId: this.formData.carStyleId,//车型id
                                sysId: this.formData.sysId,//订单来源 app：1 ； pc：2
                                productLoanPeriodDesc: this.formData.productLoanPeriodDesc,//产品融资期限
                                productRateDesc: this.formData.productRateDesc,//产品利率
                            };
                            this.submitFn(polarisserver + '/credit/createPreTrial', Params, 'post').then((res) => {
                                if (res.result) {
                                    this.showResModal = true;
                                }
                            })
                        }

                        //企业新建后可以返回列表或者去编辑

                        if (_this.isQy && !_this.isChange) {
                            let resultParams = {
                                channelCode: this.formData.channelCode,//渠道编码
                                reportSubject: this.formData.reportSubject,//主体编码
                                reportSubjectName: this.formData.reportSubjectName,//主体名称
                                productCode: this.formData.productCode,//产品编码 'M000043',
                                productVersion: this.formData.productVersion, //产品版本号  1.0
                                styleId: this.formData.carStyleId,//车型id
                                sysId: this.formData.sysId,//订单来源 app：1 ； pc：2
                                customerType: this.formData.customerType,// 客户类型
                            };
                            this.submitFn(polarisserver + '/credit/initOrderInfo', resultParams, 'post').then((res) => {
                                let orderId = res.orderNo;
                                this.$Modal.success({
                                    title: '提示',
                                    closable: false,
                                    maskClosable: false,
                                    content: '<p>订单创建成功  ！订单号：' + orderId + '</p>',
                                    okText: '编辑订单',
                                    cancelText: '返回列表',
                                    showCancel: true,
                                    onOk: () => {
                                        this.$router.replace({
                                            name: 'orderEdit',
                                            params: {
                                                orderNo: orderId,
                                                customerType: this.formData.customerType
                                            },
                                        })
                                    },
                                    onCancel: () => {
                                        this.backToList();
                                    },
                                });
                            })
                        }

                    }
                );
            },
            //产品弹窗中确认选中的产品，赋值给表单
            selectProdResFn(item) {
                if (!item || item.productCode == '') return this.errorModal('未选择产品！');
                if (this.formData.productCode != item.productCode || this.formData.productVersion != item.version) {//产品改变的时候再赋值 并且清空车型
                    this.setProductData(item);
                    this.clearCar();
                }
                this.showProdModal = false;
            },
            //选择产品按钮，点击后打开产品选择弹窗,无数据的话先执行一下查询
            openProdModalFn() {
                if (this.productInputDisabled) return false;
                this.showProdModal = true;
                this.prodModalCreate = true;
            },
            //清空产品数据
            clearProduct() {
                if (!this.inited) return false;
                this.setProductData();
            },
            //设置产品数据
            setProductData(obj) {
                obj = obj || {};
                this.formData.productCode = obj.productCode || '';
                this.formData.productName = obj.productName || '';
                this.formData.productVersion = obj.version || '';
                this.formData.productLoanPeriodDesc = obj.loanPeriodDesc || '';
                this.formData.productRateDesc = obj.rateDesc || '';
            },
            //设置车型数据
            setCarData(obj) {
                console.log(obj)
                obj = obj || {};
                this.formData.carTypeId = obj.carStyleId || "";
                this.formData.carMasterBrandId = obj.carMasterBrandId || "";
                this.formData.carMasterBrandName = obj.carMasterBrandName || "";
                this.formData.carBrandId = obj.carBrandId || "";
                this.formData.carBrandName = obj.carBrandName || "";
                this.formData.carModelId = obj.carModelId || "";
                this.formData.carModelName = obj.carModelName || "";
                this.formData.carStyleId = obj.carStyleId || "";
                this.formData.carStyleName = obj.carStyleName || "";
            },
            //清空车型数据
            clearCar() {
                if (!this.inited) return false;
                this.setCarData();
            },
            //改变渠道时，清空产品和车型
            changeChannel(val) {
                let obj = this.DICT_channelList.find(item => item.channelCode == val);
                if (!obj) return;
                this.formData.channelName = obj.channelName;//渠道简称
                this.formData.mainName = obj.mainName;//渠道全称
                let reportSubject = this.formData.reportSubject;
                if (reportSubject) {
                    let ztobj = this.DICT_channelChildList.find(item => item.reportSubject == reportSubject) || {};
                    this.formData.reportSubject = ztobj.reportSubject || '';
                    this.formData.reportSubjectName = ztobj.reportSubjectName || '';
                }
                this.clearProduct();
                this.clearCar();
            },
            //改变主体
            changeGszt(val) {
                let obj = this.DICT_channelChildList.find(item => item.reportSubject == val) || {};
                this.formData.reportSubjectName = obj.reportSubjectName || '';
                this.clearProduct();
                this.clearCar();
            },
            //改变产品时，清空车型
            changeProduct() {
                if (!this.inited) return false;
                this.clearCar();
            },
            //选择车型相关交互
            carpickerChange(item) {
                this.showCarPanel = false;
                this.setCarData(item)
            },
            //车型组件外点击时隐藏
            outsideCarInputFn() {
                this.showCarPanel = false;
            },
            //搜索框聚焦时如果为空显示默认下拉
            carstyleInputFocus() {
                if (!this.carInputDisabled) {
                    this.showCarPanel = true;
                }
            },
            carstyleInputBlur() {
            },
            //输入改变值或者点击搜索按钮时显示下拉面板
            carstyleInputChange(isbtn) {
                if (this.carInputDisabled) return false;
                if (this.showCarPanel == true) {
                    this.showCarPanel = false;
                    return false;
                }
                this.showCarPanel = true;
            },
        },
    }
</script>



