<template>
    <div class="car-default-panel" v-show="visible">
        <div class="car-picker" :class="onlyLastModel?'only-last-model':''">
            <div class="car-brand-list">
                <ul class="car-brand-letterList">
                    <template v-for="(item,index) in carBrandLetterList">
                        <li @click="!onlyLastModel && selectCarBrandLetter(item)"
                            :class="selectCar.selectLetter==item? 'active':''">
                            {{item}}
                        </li>
                    </template>
                </ul>
                <div ref="brandPanel" class="car-panel-list car-brand-showList">
                    <ul v-if="!carBrandResList || !carBrandResList.length">
                        <li class="car-panel-empty">
                            <div class="text-left withicon-empty-tip">
                                <Icon type="md-sad" size="18"/>
                                <span>暂无数据</span>
                            </div>
                        </li>
                    </ul>
                    <ul :ref="obj.letter" v-for="(obj,i) in carBrandResList" :key="obj.letter" v-else>
                        <li class="car-panel-empty" v-if="!obj.childList || !obj.childList.length">
                            <div class="text-left withicon-empty-tip">
                                <Icon type="md-sad" size="18"/>
                                <span>暂无数据</span>
                            </div>
                        </li>
                        <template v-for="(item,index) in obj.childList" v-else>
                            <li>
                                <a href="javascript:;" :class="selectCar.carMasterBrandId==item.value?'active':''"
                                   @click="!onlyLastModel && selectCarBrand(item,obj.letter)">
                                    {{obj.letter}} {{item.name}}</a>
                            </li>
                        </template>
                    </ul>
                </div>
            </div>
            <div class="car-series-list animated" :class="{slideInRight:showCarSeriesList}" v-show="showCarSeriesList">
                <div class="car-panel-list car-series-showList">
                    <div class="car-panel-empty">全部车系</div>
                    <ul v-if="!carSeriesResList || !carSeriesResList.length">
                        <li class="car-panel-empty">
                            <div class="text-left withicon-empty-tip">
                                <Icon type="md-sad" size="18"/>
                                <span>暂无数据</span>
                            </div>
                        </li>
                    </ul>
                    <ul v-for="(obj,i) in carSeriesResList" :key="obj.entityId" v-else>
                        <li class="car-panel-group-title">{{obj.name}}</li>
                        <li class="car-panel-empty" v-if="!obj.modelDTOList || !obj.modelDTOList.length">
                            <div class="text-left withicon-empty-tip">
                                <Icon type="md-sad" size="18"/>
                                <span>暂无数据</span>
                            </div>
                        </li>
                        <template v-for="(item,index) in obj.modelDTOList" v-else>
                            <li>
                                <a href="javascript:;" :class="selectCar.carModelId==item.entityId?'active':''"
                                   @click="!onlyLastModel && selectCarSeries(item, obj)">{{item.name}}</a>
                            </li>
                        </template>
                    </ul>
                </div>
            </div>
            <div class="car-style-list animated" :class="{slideInRight:showCarStyleList}" v-show="showCarStyleList">
                <div class="car-panel-list car-style-showList">
                    <div class="car-panel-empty">全部车型</div>
                    <ul>
                        <li class="car-panel-empty" v-if="!carStyleResList || !carStyleResList.length">
                            <div class="text-left withicon-empty-tip">
                                <Icon type="md-sad" size="18"/>
                                <span>暂无数据</span>
                            </div>
                        </li>
                        <template v-for="(item,index) in carStyleResList" v-else>
                            <li>
                                <a href="javascript:;" :class="selectCar.carStyleId==item.id?'active':''"
                                   @click="selectCarStyle(item)">{{item.year + ' ' + item.title}}
                                    <span class="guideprice"><span class="color696969">指导价(万元)：</span><span
                                            class="color1185EE">{{item.guidePrice}}</span></span>
                                </a>
                            </li>
                        </template>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 车型默认查询列表
     * @Props: [visible: Boolean]该组件是否可见
     * @Props: [productCode: String]带入产品ID进行查询
     * @Props: [onlyLastModel: Boolean]只能选择最后一级的车型
     * @Props: [carData: Object]只显示最后一级时传入要回显的车型数据
     */

    import {scrollTo} from "@/utils/helper";

    export default {
        name: 'carStylePicker',
        props: {
            visible: {
                default: false
            },
            productCode: {
                default: ''
            },
            onlyLastModel: {
                default: false
            },
            carData: {
                default: {}
            },
        },
        data() {
            return {
                //选中车型值
                selectCar: {
                    "selectLetter": "",
                    "carMasterBrandId": "",
                    "carMasterBrandName": "",
                    "carBrandId": "",
                    "carBrandName": "",
                    "carModelId": "",
                    "carModelName": "",
                    "carStyleId": "",
                    "carStyleName": ""
                },
                //车型相关列表是否显示
                showCarBrandList: false,
                showCarSeriesList: false,
                showCarStyleList: false,
                //车型相关列表数据
                carBrandLetterList: [],
                carBrandResList: [],
                carSeriesResList: [],
                carStyleResList: [],
            }
        },
        computed: {},
        mounted() {
            //初始查询车辆品牌列表  条件  编辑的时候 change==1
            if (this.$route.query.change) {
                this.queryCarBrandList();
            }
        },
        methods: {
            //选择车型相关交互
            selectCarBrandLetter(item) {
                //选中字母后应该定位品牌
                this.selectCar.selectLetter = item;
                //滚动
                const el = this.$refs['brandPanel'];
                const total = this.$refs[item][0].offsetTop;
                //滚定到字母对应首个品牌位置
                scrollTo(el, total, 300);
            },
            //选中品牌后查询车系
            selectCarBrand(item, letter) {
                this.selectCar.selectLetter = letter;
                this.selectCar.carMasterBrandId = item.value;
                this.selectCar.carMasterBrandName = item.name;
                this.selectCar.carBrandId = this.onlyLastModel ? this.carData.carBrandId : '';
                this.selectCar.carBrandName = this.onlyLastModel ? this.carData.carBrandName : '';
                this.selectCar.carModelId = this.onlyLastModel ? this.carData.carModelId : '';
                this.selectCar.carModelName = this.onlyLastModel ? this.carData.carModelName : '';
                this.selectCar.carStyleId = this.onlyLastModel ? this.carData.carStyleId : '';
                this.selectCar.carStyleName = this.onlyLastModel ? this.carData.carStyleName : '';
                this.showCarSeriesList = this.onlyLastModel ? true : false;
                this.showCarStyleList = this.onlyLastModel ? true : false;
                let params = {
                    id: item.value,
                    productCode: this.productCode
                }
                this.$http.post(polarisserver + '/product/queryBrandList', params).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '车系请求失败');
                    this.carSeriesResList = result.data || [];
                    this.showCarSeriesList = true;
                    if (!this.onlyLastModel) return;
                    var brandObj = this.carSeriesResList.find(item => item.entityId == this.carData.carBrandId)
                    if (brandObj) {
                        var seriesItem = brandObj.modelDTOList.find(item => item.entityId == this.carData.carModelId)
                        if (seriesItem) {
                            this.selectCarSeries(seriesItem, brandObj)
                        }
                    }
                }).catch((err) => {
                    this.errorCatchModal('车系请求出错：', err);
                })
            },
            //选中车系后查询车型
            selectCarSeries(item, obj) {
                this.selectCar.carBrandId = obj.entityId;
                this.selectCar.carBrandName = obj.name;
                this.selectCar.carModelId = item.entityId;
                this.selectCar.carModelName = item.name;
                this.selectCar.carStyleId = this.onlyLastModel ? this.carData.carStyleId : '';
                this.selectCar.carStyleName = this.onlyLastModel ? this.carData.carStyleName : '';
                this.showCarStyleList = false;
                let params = {
                    id: item.entityId,
                    productCode: this.productCode
                }
                this.$http.post(polarisserver + '/product/queryModelList', params).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '车型请求失败');
                    this.carStyleResList = result.data || [];
                    this.showCarStyleList = true;
                }).catch((err) => {
                    this.errorCatchModal('车型请求出错：', err);
                })
            },
            //选中车型后的事件，赋值
            selectCarStyle(item) {
                this.selectCar.carStyleId = item.id;
                this.selectCar.carStyleName = item.title;
                this.selectCar.guidePrice = item.guidePrice;
                this.selectCar.year = item.year;
                this.showCarBrandList = this.onlyLastModel ? true : false;
                this.showCarSeriesList = this.onlyLastModel ? true : false;
                this.showCarStyleList = this.onlyLastModel ? true : false;
                this.$emit('on-change', this.selectCar);
            },
            //查询车辆品牌列表,先初始化清空所有数据
            queryCarBrandList() {
                this.carBrandLetterList.splice(0);
                this.carBrandResList.splice(0);
                this.carSeriesResList.splice(0);
                this.carStyleResList.splice(0);
                this.showCarBrandList = false;
                this.showCarSeriesList = false;
                this.showCarStyleList = false;
                if (!this.productCode) return;
                let params = {
                    productCode: this.productCode
                }
                let urlParams = '?productCode=' + (this.productCode || '')
                this.$http.post(polarisserver + '/product/queryMasterBrandList', params).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '车辆品牌请求失败');
                    let letterArr = this.carBrandLetterList;
                    let arr = result.data || [];
                    arr.sort((a, b) => {
                        return a.letter > b.letter ? 1 : -1;
                    })
                    let newArr = [], brandItem;
                    arr.forEach(item => {
                        let letter = item.letter;
                        if (letterArr.indexOf(letter) < 0) {
                            letterArr.push(letter)
                        }
                        let obj = newArr.find(row => row.letter == letter);
                        if (!obj) {
                            obj = {
                                letter: letter,
                                childList: []
                            };
                            newArr.push(obj)
                        }
                        obj.childList = obj.childList || [];
                        var itemObj = {
                            name: item.title,
                            value: item.id,
                            letter: letter
                        }
                        obj.childList.push(itemObj)
                        if (!this.onlyLastModel) return;
                        if (itemObj.value === this.carData.carMasterBrandId) {
                            brandItem = itemObj;
                        }
                    })
                    this.carBrandResList = newArr;
                    if (brandItem) {
                        this.selectCarBrand(brandItem, brandItem.letter)
                    }
                }).catch((err) => {
                    this.errorCatchModal('车辆品牌请求出错：', err);
                })
            },
        },
        watch: {
            productCode(val) {
                this.queryCarBrandList()
            }
        }
    }
</script>



