<template>
    <Modal title="选择产品" width="650" v-model="showModal" :mask-closable="true" :closable="true" @on-cancel="closeModal"
           footer-hide @on-visible-change="visibleChange">
        <i-form :model="formData" ref="formData" :label-width="120" style="height: 380px;" @submit.native.prevent>
            <!--搜索关键词-->
            <div class="search-input-con" style="margin-bottom:10px;margin-top:-10px;">
                <Button type="text" custom-icon="iconfont icon-search" class="search-input-icon" @click="inputChange"
                        v-if="!formData.q"></Button>
                <i-input ref="searchInput" v-model="formData.q" @on-focus="inputFocus" @on-change="searchInputkeyup"
                         clearable placeholder="请搜索">
                </i-input>
                <!--搜索关键字的结果列表-->
                <div v-if="searchQ" class="bg-shadow product-search-res">
                    <ul>
                        <li v-for="(item,index) in tableListData" @click="setSelectProd(item)">
                            <span class="searchCon">{{item.productName}}</span>
                            <span class="searchCon color696969">结算利率(%)：{{item.rateDesc}}</span>
                            <span class="searchCon color696969">贷款期限(期)：{{item.loanPeriodDesc}}</span>
                        </li>
                        <li v-if="!tableListData.length" class="product-empty">未搜索到结果</li>
                    </ul>
                </div>
            </div>

            <!--查询全部的结果列表-->
            <div v-if="!searchQ" class="product-table-box">
                <i-table height="300" :loading="loading"
                         border :columns="columns" :data="tableListData"></i-table>
                <div class="margin-t-10">
                    <Page size="small" :total="pager.total" :page-size="formData.pageSize"
                          :current="formData.currentPage" :page-size-opts="pager.Opts" @on-change="pageChange"
                          @on-page-size-change="pageSizeChange"></Page>
                </div>
            </div>
        </i-form>
        <!--常用产品标签-->
        <div class="margin-t-10">
            <div class="font0 clearfix">
                <div class="hot-product-title">常用的产品
                </div>
                <div class="hot-product-con">
                    <span v-for="(item,index) in hotProductList"
                          @click='setSelectProd(item)'>{{item.productName}}</span>
                </div>
            </div>
        </div>
    </Modal>
</template>
<script>
    /**
     * @Description: 产品搜索弹窗，单选
     * @Props: [channelCode: String] 渠道ID
     * @Props: [reportSubject: String] 主体ID
     * @Props: [value: String]绑定的搜索关键字
     * @Props: [visible: Boolean]该组件是否可见
     */

    export default {
        name: 'prodSingleSelectModal',
        props: {
            channelCode: {
                default: ''
            },
            reportSubject: {
                default: ''
            },
            value: {
                default: false
            },
            visible: {
                default: false
            },
        },
        data() {
            return {
                showModal: false,
                showClassTab: true, //显示切换产品大类标签
                userAccount: this.$store.state.app.userInfo.userAccount,
                loading: false, //产品表格加载状态
                pager: {
                    //产品列表的分页
                    "total": 0,
                    "Opts": [10, 20, 50, 100, 200]
                },
                formData: {
                    //选择产品弹窗中数据
                    "currentPage": 1,
                    "pageSize": 10,
                    "q": "",
                },
                selectProdId: '', //单选按钮对应的产品id
                selectProdRow: { //产品选择弹窗中选中的值
                    prodId: '',
                    prodName: '',
                },
                columns: [ //产品表格列
                    {
                        title: '序号',
                        width: 55,
                        align: 'left',
                        render: (h, params) => {
                            return h('span', params.index + (this.formData.currentPage - 1) * this.formData.pageSize + 1);
                        }
                    },
                    {
                        title: '操作',
                        key: 'oper',
                        width: 80,
                        align: 'center',
                        render: (h, params) => {
                            let that = this;
                            return h('a', {
                                on: {
                                    'click'() {
                                        that.setSelectProd(params.row);
                                    }
                                }
                            }, '选择');
                        }
                    },
                    {
                        title: '产品方案名称',
                        key: 'productName',
                    },
                    {
                        title: '结算利率(%)',
                        key: 'rateDesc',
                        width: 110,
                    },
                    {
                        title: '贷款期限(期)',
                        key: 'loanPeriodDesc',
                        width: 110,
                    },
                ],
                tableListData: [], //产品表格数据
                hotProductList: [],//常用的产品
                searchResult: false,
                timer: undefined,
            }
        },
        computed: {
            searchQ() {
                return this.formData.q.replace(/(^\s*)|(\s*$)/g, "");
            }
        },
        mounted() {
            //初始查询列表
            this.showModal = this.value;
        },
        methods: {
            //表格翻页
            pageChange(curPage) {
                this.formData.currentPage = curPage;
                this.queryList();
            },
            //表格修改每页显示数量
            pageSizeChange(pageSize) {
                this.formData.currentPage = 1;
                this.formData.pageSize = pageSize;
                this.queryList();
            },
            //查询产品列表前先清空选择
            queryList() {
                this.selectProdId = '';
                this.tableListData = [];
                let formData = this.formData;
                //查询列表及数据
                let params = {
                    channelCode: this.channelCode,
                    reportSubject: this.reportSubject,
                    productName: this.searchQ,
                    current: formData.currentPage,
                    rowCount: formData.pageSize,
                    account: this.userAccount//用户
                };
                this.loading = true;
                this.$http.post(polarisserver + '/channel/product/page', params).then((res) => {
                    this.loading = false;
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '产品列表请求失败');
                    this.pager.total = result.data.total;
                    this.tableListData = result.data.data || [];
                }).catch((err) => {
                    this.loading = false;
                    this.errorCatchModal('产品列表请求出错：', err);
                })
            },
            //获取常用产品的数据
            queryhotProductList() {
                this.$http.get(polarisserver + '/product/getCommonlyProductInfo' + '/' + this.userAccount, {}).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '常用产品请求失败');
                    this.hotProductList = result.data || [];
                }).catch((err) => {
                    this.errorCatchModal('常用产品请求出错：', err);
                })
            },
            //产品输入框交互
            inputFocus() {
                this.showClassTab = this.searchQ == '';
            },
            //鼠标按下事件
            searchInputkeyup() {
                if (this.searchQ == null || this.searchQ == "") {
                    this.searchResult = false;
                    this.queryList();
                } else {
                    this.searchResult = true;
                    this.inputChange();
                }
                let t = setTimeout(() => {
                    this.$refs['searchInput'].focus();
                    clearTimeout(t);
                    t = undefined;
                }, 200);
            },
            //搜索文字改变时修改组件传值，并将产品大类和分页相关数据清空
            inputChange() {
                if (this.searchQ && this.searchQ.length > 0) {
                    //获取当前延时函数的ID，便于后面clearTimeout清除该ID对应的延迟函数
                    this.timer = setTimeout(() => {
                        this.showClassTab = false;
                        this.formData.currentPage = 1;
                        this.queryList();
                        clearTimeout(this.timer);
                        this.timer = undefined;
                    }, 100);
                }
            },
            //选中的数据赋值，触发回填，直接选择并关闭弹窗
            setSelectProd(row) {
                this.selectProdRow = row;
                this.selectProdId = row.productCode;
                this.confirmProdChooseFn();
            },
            //产品弹窗中确认选中的产品，赋值给表单
            confirmProdChooseFn() {
                if (!this.selectProdRow || this.selectProdRow.selectProdId == '') return this.$Message.error('未选择产品！');
                this.$emit('on-select', this.selectProdRow);
            },
            //关闭
            closeModal() {
                this.$emit('on-close');
            },
            //模板状态切换时执行查询
            visibleChange() {
                if (this.showModal == true)
                    this.queryList();
                this.queryhotProductList();
            }
        },
        watch: {
            //监听
            value(val) {
                if (val) {
                    this.formData.q = "";
                }
                this.showModal = val;

            },
            "formData.q"(newval, oldval) {
                if (newval == "") {
                    this.queryList();
                }
            }
        }
    }
</script>



